// import yzCanvasPoster from '@/components/yz-canvas-poster/yz-canvas-poster.vue'
export default {
	components: {
		// yzCanvasPoster
	},
	data() {
		return {
			// ifShow: false,
			// imageBg: '../../static/img/icons/bg.jpg',
			// imageHead: ['../../static/img/icons/zhongjiang.png', 50, 20, 40, 40],
			// imageBody: ['../../static/img/icons/zhongjiang.png', 98, 240, 120, 120],
			// textHead:  ['myCanvas', 100, 40, '#ffffff', '20']
		}
	},
	methods: {
		// 小程序
		savePosterPath(url){
			uni.downloadFile({
				url,
				success: (resFile) => {
				  console.log(resFile, "resFile");
				  if (resFile.statusCode === 200) {
					uni.getSetting({
					  success: (res) => {
						if (!res.authSetting["scope.writePhotosAlbum"]) {
						  uni.authorize({
							scope: "scope.writePhotosAlbum",
							success: () => {
							  uni.saveImageToPhotosAlbum({
								filePath: resFile.tempFilePath,
								success: (res) => {
								  return uni.showToast({
									title: "保存成功！",
								  });
								},
								fail: (res) => {
								  return uni.showToast({
									title: res.errMsg,
								  });
								},
								complete: (res) => {},
							  });
							},
							fail: () => {
							  uni.showModal({
								title: "您已拒绝获取相册权限",
								content: "是否进入权限管理，调整授权？",
								success: (res) => {
								  if (res.confirm) {
									uni.openSetting({
									  success: (res) => {
										console.log(res.authSetting);
									  },
									});
								  } else if (res.cancel) {
									return uni.showToast({
									  title: "已取消！",
									});
								  }
								},
							  });
							},
						  });
						} else {
						  uni.saveImageToPhotosAlbum({
							filePath: resFile.tempFilePath,
							success: (res) => {
							  return uni.showToast({
								title: "保存成功！",
							  });
							},
							fail: (res) => {
							  return uni.showToast({
								title: res.errMsg,
							  });
							},
							complete: (res) => {},
						  });
						}
					  },
					  fail: (res) => {},
					});
				  } else {
					return uni.showToast({
					  title: resFile.errMsg,
					});
				  }
				},
				fail: (res) => {
				  return uni.showToast({
					title: res.errMsg,
				  });
				},
			  });
		},
		// APP-PLUS
		savePhoto(url){
			// 先下载图片
			uni.downloadFile({
				url,
				success: (res) => {
					// 获取到图片本地地址后再保存图片到相册（因为此方法不支持远程地址）
					uni.saveImageToPhotosAlbum({
						filePath: res.tempFilePath,
						success: () => {
							uni.showToast({
								title: "保存成功！",
							});
						},
						fail: () => {
						  uni.showToast({
							title: "保存失败",
						  });
						},
					});
				},
			});
		},
		canvasShow() {
			// this.ifShow = true;
		}
	}
}
